html{
    font-family: 'Roboto', sans-serif;
    
}
* {
    margin: 0;
    border: 0;
}
.NavBar{
    display: flex;
    position: fixed;
    bottom: 5;
    width: 100%;
    background-color: #6e48aa;

}

model-viewer {
    position: fixed;
    left: auto;
    right: 0%;
    width: 100vw;
    height: 80%;
}

.TituloLocal{
    display: flex;
    flex-direction: column;
    width: 100%;
    align-self: center;
    text-align: center;
    justify-content: center;
    padding-top: 2%;
    padding-bottom: 10px;
}

#annotation {
    background-color: #b3b1b1;
    position: absolute;
    transform: translate(10px, 10px);
    border-radius: 10px;
    padding: 10px 25px;
    --min-hotspot-opacity: 0;
    width: 5vw;
}

.questionArea{
    position: absolute;
    bottom: 5%;
    width: 100%;
    padding: 0 10%;
}
.BoxQuest{
    background-color: gray;
    padding: 2%;
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 2px 5px 10px #888888;
}
.btnQuest{
    padding: 3% 4%;
    width: 30%;
    margin: 2% 0;
    background-color: #3498DB;
    border-radius: 5px;
    box-shadow: 2px 5px 10px #888888;
    color: white;
}
.btnQuest:active{
    background-color: rgb(71, 71, 71);
}
.rsc{
    display: flex;
    justify-content: center;
    margin: 25px 15px;
    border: none;
}
.rsc:hover{
    border: none;
}
.rsc-header-close-button{
    display: flex;
}
.inputCad{
    width: 100%;
    border-color: gray;
    border-radius: 5px;
    padding: 50px;
}
.boxText{
    display: flex;
    align-items: center;
    background-color: #3498DB;
    color: white;
    border-radius: 15px;
    padding: 5px 10px;
    box-shadow: 2px 5px 10px #888888;
}

.imgLogo{
    display: flex;
    width: 100%;
    box-shadow: 2px 5px 10px #888888;
}

.Button-NavBar{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 13px;
    color: white;
    text-decoration: none;
}
.img-NavBar{
    width: 25px;
}
.Button-NavBar:active{
    background-color: white;
    opacity: 0.8;
}
@media screen and (max-width: 568px) {
    .rsc{
      
    }
}